import { useEffect, useState } from 'react';
import { CreateSubCategory } from '../../redux/actions/SubcategoryAction'
import { useSelector, useDispatch } from 'react-redux'
import notify from '../../hook/useNotifaction'
import { GetAllCategory } from '../../redux/actions/CategoryAction'

const AddSubcategoryhook = () => {

    const dispatch = useDispatch();
    useEffect(() => {
        if (!navigator.onLine) {
            notify("    ", "warn")
            return;
        }
        dispatch(GetAllCategory());
    }, [dispatch])
    const [id, setID] = useState('0')
    const [name, setName] = useState('')
    const [loading, setLoading] = useState(true)
    //get last catgeory state from redux
    const category = useSelector(state => state.allCategory.category)


    //get last sub catgeory state from redux
    const subcategory = useSelector(state => state.subCategory.subcategory)

    //on change dropdown menu
    const handelChange = (e) => {
        console.log(e.target.value)
        setID(e.target.value)
    }

    //to save name
    const onChangeName = (e) => {
        e.persist();
        setName(e.target.value)
    }
    //on save data 
    const handelSubmit = async (e) => {
        e.preventDefault();
        if (!navigator.onLine) {
            notify("    ", "warn")
            return;
        }
        if (id === "0") {
            notify("    ", "warn")
            return;
        }
        if (name === "") {
            notify("    ", "warn")
            return;
        }

        setLoading(true)
        await dispatch(CreateSubCategory({
            name,
            category: id
        }))
        setLoading(false)

    }
    useEffect(() => {

        if (loading === false) {
            setName("")
            setID("0")
            if (subcategory)
                console.log(subcategory)
            if (subcategory.status === 201) {
                notify("  ", "success")
            }
            else if (subcategory === "Error Error: Request failed with status code 400") {
                notify("       ", "warn")
            }
            else {
                notify("    ", "warn")
            }

            setLoading(true)
        }
    }, [loading,subcategory])


    return [id, name, loading, category, subcategory, handelChange, handelSubmit, onChangeName]
};

export default AddSubcategoryhook;
