import React from 'react'
import Subtitle from '../Utility/Subtitle'
import { Container, Row } from 'react-bootstrap'
import prod1 from '../../assets/images/prod1.png'
import prod3 from '../../assets/images/prod3.png'
import prod4 from '../../assets/images/prod4.png'
import CategoryCard from '../Category/CategoryCard'

const Homecategory = ({title,pathText,btntitle}) => {
    return (
        <Container>
            <Subtitle btntitle={btntitle} pathText={pathText} title={title} />
            <Row >
                <CategoryCard background="dodgerblue" title={" HP"} img={prod1}/>
                <CategoryCard background="gold" title={" lenovo"} img={prod1}/>
                <CategoryCard background="darkcyan" title={" tefal"} img={prod3}/>
                <CategoryCard background="orange" title={" ceramic"} img={prod3}/>
                <CategoryCard background="darkturquoise" title={" "} img={prod4}/>
                <CategoryCard background="lightsalmon" title={" "} img={prod4}/>
            </Row>
        </Container>
    )
}

export default Homecategory
